.login{
    display:flex;
    place-items:center;
    flex-direction: column;
    margin-left:auto;
    margin-right:auto;
    padding-top:100px;
    padding-bottom: 10px;
}

.imgclass {
    object-fit: contains;
    height:70px;
    margin-top: 20px;
    margin-bottom: 20px;

}

.login > form{
    display: flex;
    flex-direction: column;

}

.login > form > input{
    width: 350px;
    height: 30px;
    font-size:20px;
    padding-left: 10px;
    margin-bottom: 10px;
    border-radius: 5px;
}

.login > form > button {
    width: 350px;
    height: 30px;
    font-size: large;
    color:#fff;
    background-color: #0074b1;
    border-radius: 5px;
}

.login_register{
    color: #0074b1;
    cursor: pointer;
}

.login > p {
  margin-top: 20px;
    
}