.head {
    display:flex;
    justify-content: space-evenly;
    border-bottom: 0.1px solid lightgrey;
    padding-top: 10px;
    padding-bottom: 10px;
    width:100%;
    position: sticky;
    background-color: white;
    z-index: 999;
}

.header_left{
    display:flex;
}

.header_left > img{
    object-fit : contain;
    height:40px;
    margin-right:10px;
}

.header_search{
    padding:10px;
    display:flex;
    align-items: center;
    border-radius:5px;
    height:22px;
    color:gray;
    background-color:#eef3f8;
}

.header_search > input {
    outline: none;
    border:none;
    background: none;
}
.header_right{
    display: flex;
}

